import { RegisterComponent } from '@/common/register-component';
import { MethodKeys } from '@/common/types';
import { NylasMailboxConnectorInterface } from '@/connector/connector-interface';
import { debug } from '@/utils/utils';
import { Component, Element, Host, Prop, h } from '@stencil/core';
import { ObservableMap } from '@stencil/store';

export type Stores = Record<string, ObservableMap<any>>;
export type PublicMethodsOfNylasMailboxConnector = Pick<NylasMailboxConnectorInterface, MethodKeys<NylasMailboxConnectorInterface>>;
export type PropertyPaths<T> = T extends object
  ? {
      [K in keyof T]: T[K] extends Function ? `${K & string}` : `${K & string}.${PropertyPaths<T[K]>}`;
    }[keyof T]
  : '';
export type MethodsOfNylasMailboxConnector = PropertyPaths<NylasMailboxConnectorInterface>;

@Component({
  tag: 'nylas-mailbox-toolbar-button',
  styleUrl: 'nylas-mailbox-toolbar-button.scss',
  shadow: true,
})
export class NylasMailboxToolbarButton {
  @Element() private host!: HTMLNylasMailboxToolbarButtonElement;

  /**
   * The name of the mailbox connector method to call.
   */
  @Prop({ attribute: 'method' }) readonly method!: MethodsOfNylasMailboxConnector;

  /**
   * The value of the state to compare against to check if the data is loading, error, or ready.
   */
  @Prop({ attribute: 'data-state' }) readonly dataState?: string;

  /**
   * If true, then the component will render the icon only.
   */
  @Prop() readonly iconOnly: boolean = false;

  /**
   * If true, then the component will render if the state is true.
   * This prop is used with the `state` prop.
   */
  @Prop() readonly mailboxConnector?: NylasMailboxConnectorInterface;

  constructor() {}

  connectedCallback() {}

  disconnectedCallback() {}

  async componentWillLoad() {
    debug(`[nylas-if-state] Component will load`);
  }

  async componentDidLoad() {
    debug(`[nylas-if-state] Component did load`);
  }

  /**
   * The click handler for the button.
   */
  private onClick = async () => {
    const method = this.method as unknown as string;
    debug(`[nylas-mailbox-toolbar-button] onClick`, { method, mailboxConnector: this.mailboxConnector });
    if (this.mailboxConnector && method) {
      const methodToCall = this.callMethod(this.mailboxConnector, method);
      if (methodToCall) {
        await methodToCall();
      }
    }
  };

  /**
   * Traverses the object to find the method.
   * @param obj The object to traverse.
   * @param accessString The string to access the method.
   * @returns The method if it exists, undefined otherwise.
   */
  private callMethod(obj: any, accessString: string): (() => void) | undefined {
    // Split the string into parts
    const parts = accessString.split('.');

    // Traverse the object to find the method
    let current = obj;
    for (const part of parts.slice(0, -1)) {
      current = current[part];
      if (!current) {
        return undefined;
      }
    }

    // Get the method
    const methodName = parts[parts.length - 1];
    const method = current[methodName];

    // Check if the method is a function
    if (typeof method === 'function') {
      return method.bind(current);
    }

    return undefined;
  }

  /**
   * Renders the slot based on the state.
   * @param state The state of the data.
   */
  private renderByState(state: boolean) {
    return state ? <slot name="loading">Loading...</slot> : <slot>Click</slot>;
  }

  /**
   * Checks if the slot exists.
   * @param name The name of the slot.
   * @returns True if the slot exists, false otherwise.
   */
  private hasSlot(name: string) {
    return !!this.host.querySelector(`[slot="${name}"]`);
  }

  @RegisterComponent({
    name: 'nylas-mailbox-toolbar-button',
    connectorToProp: 'mailboxConnector',
    fireRegisterEvent: true,
  })
  render() {
    let state = false;
    const store = this.mailboxConnector?.nylasMailboxStore;
    if (typeof store !== 'undefined') {
      const storeKey = String(this.dataState) as keyof typeof store.state;
      state = this.dataState ? store.get(storeKey) === 'loading' : false;
    }
    const key = state ? `state-loading` : 'state-ready';
    return (
      <Host>
        <sp-theme scale="medium" color="dark">
          <sp-button onClick={this.onClick} disabled={state} key={key} icon-only={this.iconOnly} label={this.renderByState(state)}>
            {this.hasSlot('icon') && (
              <div slot="icon" class={'slot'}>
                <slot name="icon" />
              </div>
            )}
            {!this.iconOnly && this.renderByState(state)}
          </sp-button>
        </sp-theme>
      </Host>
    );
  }
}
